import React from 'react'
import { IoLockClosed } from 'react-icons/io5';

interface Partida {
  home: string,
  guest: string,
  data: string
}

export default function AvailableMatches() {
  const partidas: Partida[] = [
    {
      home: "Flamengo",
      guest: "Corinthians",
      data: "16/05/2021 16h30"
    },
    {
      home: "Palmeiras",
      guest: "São Paulo",
      data: "21/05/2021 19h00"
    }
  ];

  return (
    <section className="container" style={{ paddingTop: '1.5rem' }}>
      <div className="section-title"><p>Partidas Disponíveis</p></div>
      <div className="league-name">Campeonato Brasileiro Série A</div>
      <div className="matches-list">
        {partidas.map((p) => <>
          <div className="match">
            <p><strong>{p.home} x {p.guest} - {p.data}</strong></p>
            <div className="wagers">
              <table className="wagers-table">
                <thead>
                  <tr>
                    <th>Casa</th>
                    <th>Empate</th>
                    <th>Fora</th>
                    <th>+</th>
                  </tr>
                </thead>
                <tbody>
                  <tr style={{ height: 10 }}></tr>
                  <tr>
                    <td className="odd">1.40</td>
                    <td className="odd"><IoLockClosed /></td>
                    <td className="odd">2.67</td>
                    <td className="points">+8</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>

        </>)}
      </div>
    </section>
  )
}
